@import "../../style/variables.scss";

.app {
  &__banner {
    width: 100%;
    height: 100px;
    background-color: $dark;
    padding: 0px 25px 0px 45px;
    display: grid;
    grid-template-columns: 152px auto 133px;

    &-text {
      font-family: var(--font-family);
      font-weight: 700;
      font-size: 24px;
      color: #fff;
      font-weight: bold;
      line-height: 32px;
      padding-left: 83px;
      padding-top: 18px;
    }
  }
}
